
import { UiController } from './uiController';

const DOCUMENT_BROWSER_EXTENSION_ID = 'Autodesk.DocumentBrowser';

const av = Autodesk.Viewing;

/**
 * Adds a toolbar button that opens a Panel displaying all models
 * and views available from the loaded Document. The panel allows
 * navigating to any model referenced by the Document.
 *
 * The extension id is: `Autodesk.DocumentBrowser`
 *
 * @example
 *   viewer.loadExtension('Autodesk.DocumentBrowser')
 *
 * @memberof Autodesk.Viewing.Extensions
 * @alias Autodesk.Viewing.Extensions.DocumentBrowser
 * @see {@link Autodesk.Viewing.Extension} for common inherited methods.
 * @class
 */

export class DocumentBrowser extends av.Extension {

    constructor(viewer, options) {
        super(viewer, options);
        this.onHyperlinkListener = this.onHyperlinkListener.bind(this);
    }

    load() {
        this.viewer.addEventListener(av.HYPERLINK_EVENT, this.onHyperlinkListener);
        // Extension does all its initialization as soon as the toolbar ui is present.
        return true;
    }

    unload() {
        if (this.ui) {
            this.ui.destroy();
            this.ui = null;
        }
        this.viewer.removeEventListener(av.HYPERLINK_EVENT, this.onHyperlinkListener);
        return true;
    }

    onHyperlinkListener() {
        this._stashUI();
    }

    onToolbarCreated() {

        if (this.viewer.model) {
            this._hookToModel(this.viewer.model);
        } else {
            this.viewer.addEventListener(av.MODEL_ROOT_LOADED_EVENT, (event) => {
                this._hookToModel(event.model);
            }, { once: true });
        }
    }

    /**
     * Unloads the current model and then loads the next model in the Document.
     * It may reload the same model if the Document contains only 1 model.
     *
     * @param {object} [viewerConfig]
     * @param {object} [loadOptions]
     *
     * @alias Autodesk.Viewing.Extensions.DocumentBrowser#loadNextModel
     */
    loadNextModel(viewerConfig, loadOptions) {
        if (!this.currNode) return;

        const index = this.geometries.indexOf(this.currNode);
        const nextIndex = (index + 1) % this.geometries.length;
        this.currNode = this.geometries[nextIndex];

        const nextConfig = this.viewer.config;
        if (!Array.isArray(nextConfig.extensions)) {
            nextConfig.extensions = [];
        }
        if (nextConfig.extensions.indexOf(DOCUMENT_BROWSER_EXTENSION_ID) === -1) {
            nextConfig.extensions.push(DOCUMENT_BROWSER_EXTENSION_ID);
        }

        this.viewer.loadDocumentNode(this.rootNode.getDocument(), this.currNode, loadOptions);
    }

    /**
     * Unloads the current model and then loads the previous model in the Document.
     * It may reload the same model if the Document contains only 1 model.
     *
     * @param {object} [viewerConfig]
     * @param {object} [loadOptions]
     *
     * @alias Autodesk.Viewing.Extensions.DocumentBrowser#loadPrevModel
     */
    loadPrevModel(viewerConfig, loadOptions) {
        if (!this.currNode) return;

        const index = this.geometries.indexOf(this.currNode);
        const prevIndex = index === 0 ? (this.geometries.length-1) : (index-1);
        this.currNode = this.geometries[prevIndex];

        const nextConfig = this.viewer.config;
        if (!Array.isArray(nextConfig.extensions)) {
            nextConfig.extensions = [];
        }
        if (nextConfig.extensions.indexOf(DOCUMENT_BROWSER_EXTENSION_ID) === -1) {
            nextConfig.extensions.push(DOCUMENT_BROWSER_EXTENSION_ID);
        }

        this.viewer.loadDocumentNode(this.rootNode.getDocument(), this.currNode, loadOptions);
    }


    /**
     *
     * @param {Autodesk.Viewing.BubbleNode} bubbleNode
     * @returns {Promise} - Resolves when the model has been loaded.
     * @private
     */
    _changeModel(bubbleNode) {

        const multipageExtension = this.viewer.getExtension('Autodesk.Multipage');

        // In case of multipage, no need to load the document node here. The extension takes care of it internally.
        if (multipageExtension && multipageExtension.focusOnNode(bubbleNode)) {
            return Promise.resolve();
        }

        const nextConfig = this.viewer.config;
        if (!Array.isArray(nextConfig.extensions)) {
            nextConfig.extensions = [];
        }
        if (nextConfig.extensions.indexOf(DOCUMENT_BROWSER_EXTENSION_ID) === -1) {
            nextConfig.extensions.push(DOCUMENT_BROWSER_EXTENSION_ID);
        }

        this._stashUI();

        return this.viewer.loadDocumentNode(bubbleNode.getDocument(), bubbleNode);
    }

    /**
     * @param model
     * @private
     */
    _hookToModel(model) {
        var docNode = model.getDocumentNode();
        if (!docNode) return;

        this.currNode = docNode.findParentGeom2Dor3D();
        this.rootNode = docNode.getRootNode();
        this.geometries = this.rootNode.search({'type':'geometry'});

        let cache = this.getCache();
        if (cache.ui) {
            this.ui = cache.ui;
            cache.ui = null;
        } else {
            this.ui = new UiController(this.viewer);
        }
        this.ui.createUi( this.currNode, this.options );
        this.ui.setChangeModelHandler(this._changeModel.bind(this));
    }

    /**
     * @private
     */
    _stashUI() {
        let cache = this.getCache();
        if (this.ui) {
            this.ui.notifyStash();
            cache.ui = this.ui;
            this.ui = null;
        }
    }
}

av.theExtensionManager.registerExtension(DOCUMENT_BROWSER_EXTENSION_ID, DocumentBrowser);
